export const getResumeData = async () => {
  try {
    const data = await fetch("https://dvotud-8080.preview.csb.app/resume/me") // 向 requestURL 發送請求
      .then((response) => response.json()); // 取得伺服器回傳的資料並以 JSON 解析
    return data;
  } catch {
    return null;
  }

  // https://87dhy9.sse.codesandbox.io/resume/me
  // https://dvotud-8080.preview.csb.app/
  // .then((data) => console.log(data)); 取得解析後的 JSON 資料
};
