import { useState, useReducer, useEffect, createContext } from "react";
import { getResumeData } from "../api/resume";

const defaultValue = {
  userInfo: {
    name: "",
    engName: "",
    jobTitle: "",
    introduction: "",
    location: "",
    email: "",
    phone: ""
  },
  companys: [],
  projects: [],
  skills: [],
  isLoaded: false,
  updateUserInfo: () => {}
};

// 建立Context
export const Context = createContext(defaultValue);
Context.displayName = "ResumeContext";

// reducer 區塊
// const userInfoReducer = (state, action) => {
//   console.log("This is reducer function");
// };

// 建立 Context Provider
export const Provider = ({ children }) => {
  const [userInfo, setUserInfo] = useState(defaultValue.userInfo);
  const [companys, setCompany] = useState(defaultValue.companys);
  const [projects, setProject] = useState(defaultValue.projects);
  const [skills, setSkill] = useState(defaultValue.skills);
  const [isLoaded, setIsLoaded] = useState(defaultValue.isLoaded);

  // const [updatedUserInfo, dispatchUserInfo] = useReducer(
  //   userInfoReducer,
  //   defaultValue.UserInfo
  // );
  // const updateUserInfo = (index, label, payload) => {
  //   dispatchUserInfo({
  //     type: "UPDATE_EDUCATION_ITEM",
  //     index,
  //     label,
  //     payload
  //   });
  // };

  useEffect(() => {
    getResumeData().then((data) => {
      if (data !== null) {
        const userInfoData = data?.info;
        setUserInfo(userInfoData);
        const companyData = data?.company;
        setCompany(companyData);
        const projectData = data?.project;
        setProject(projectData);
        const skillData = data?.skill;
        setSkill(skillData);
        setIsLoaded(true);
      }
    });
  }, []);

  return (
    <Context.Provider
      value={{
        userInfo,
        companys,
        projects,
        skills,
        isLoaded
      }}>
      {children}
    </Context.Provider>
  );
};
